

.header-title{
  text-align: center;
  justify-content: center;
}

.header-container{
  display:flexbox;
  text-align: center;
  justify-content: center;
  height: 100%;
  width:100%;
  row-gap: 24px;
  padding:50px;
  background-color: rgb(214, 205, 206);
  margin-bottom: 20px;
}

.input-field{
  height: 30px;
  width: 200px;
}

/* Movie list component */
.movie-list{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
  column-gap: 24px;
  row-gap: 24px;
  justify-content: center;
    align-items: center;
}

.movie-container{
  width: 300px;
  
}

.movie-poster{
  height: 215px;
  width: 300px;
  object-fit: cover;
  background-color: teal;
}

.movie-specs{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}

.movie-name{
  font-size: 20px;
  font-weight: bold;
  font-family: 'Roboto', sans-serif;
}

.movie-ratings{
  font-size: 15px;
  /* font-weight: bold; */
}

.movie-summary{
  font-family: 'Fira Sans';
  font-size: 17px;
  text-align: justify;
}
